.header {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 33px 258px 30px 255px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(141, 109, 141, 0.3);
  // outline: 2px dashed rgb(197, 142, 122);

  @media (max-width: 1024px) {
    padding: 20px 20px 20px 25px;
  }

  @media (max-width: 727px) {
    padding: 15px 10px 5px 15px;
    align-items: flex-start;
  }
}

.header img {
  width: 202px;
  height: auto;

  @media (max-width: 727px) {
    width: 46px;
  }
}

.header__logo-link {
  width: 202px;
  height: 52px;

  @media (max-width: 727px) {
    width: 46px;
  }
}

.header__contacts-list {
  @include list-reset();
  // display: grid;
  // grid-template-columns: repeat(2, minmax(210px, auto));
  // grid-column-gap: 10px;
  // grid-row-gap: 10px;
  display: flex;
  flex-wrap: wrap;
  padding: 0 40px;
  align-items: center;
  margin: 0 0 0 auto;
  row-gap: 10px;

  @media (max-width: 1024px) {
    justify-content: space-between;
    padding: 0;
    column-gap: 25px;
  }

  @media (max-width: 727px) {
    column-gap: 10px;
  }
}

.header__contacts-list li {
  min-width: 220px;

  @media (max-width: 1024px) {
    min-width: auto;
  }
}

.header__contacts-link {
  @include hover-focus-activity();
  font-family: "PT Mono", "Courier", monospace;
  display: flex;
  align-items: center;
  column-gap: 10px;
}

.header__contacts-link span {
  @media (max-width: 1024px) {
    display: none;
  }
}

.header__contacts-link svg {
  @media (max-width: 1024px) {
    width: 34px;
    height: auto;
  }
}

.header__recall-link {
  @include btn-like-link-mixin();
  @include hover-focus-activity();

  @media (max-width: 1024px) {
    display: none;
  }
}
