  background-repeat: no-repeat;
  background-position: 50% top;
  background-color: #1b2d37;
  background-size: 100%; }

a {
  text-decoration: none;
  color: inherit; }

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0; }

